.module-signin-container{display:flex;flex-direction:column;height:100vh;overflow:auto}.module-signin-content{padding:32px 0;flex:1;background:#0087ea}.module-signin-footer{background:white;height:100px;display:flex;justify-content:center;align-items:center;flex-direction:column}.module-signin-footer div{text-align:center}.module-signin-footer .module-signin-text-forgot{display:flex;flex-direction:row}@media (min-width:768px){.module-signin-content{padding:112px 0 24px 0}}.module-signin-top{text-align:center}.module-signin-header{display:flex;flex-direction:column;align-items:center}.module-signin-header a{text-decoration:none}.module-signin-logo{height:25px;vertical-align:top}.module-signin-desc{font-size:15px;font-family:Helvetica,"Helvetica Neue",Arial,sans-serif;color:white;font-weight:300;margin-bottom:40px}.module-signin-main{width:368px;margin:0 auto}@media screen and (max-width:576px){.module-signin-main{width:95%}}.module-signin-main .icon{font-size:24px;color:rgba(0,0,0,0.2);margin-left:16px;vertical-align:middle;cursor:pointer;transition:color 0.3s}.module-signin-main .icon:hover{color:#1890ff}.module-signin-main .module-signin-other{text-align:left;margin-top:24px;line-height:22px}.module-signin-main .module-signin-other .module-signin-register{float:right}.module-signin-login .module-signin-submit{width:100%}.module-signin-btn{border-radius:25px;display:flex;flex-direction:row;align-items:center;padding:10px 25px;margin-bottom:20px;cursor:pointer}.module-signin-btn.module-signin-btn-facebook{background-color:#096dd9;color:white}.module-signin-btn.module-signin-btn-google{background-color:#EA4235;color:white}.module-signin-btn .module-signin-text-login{text-align:center;width:100%}.module-signin-btn:hover{box-shadow:0 0 20px rgba(185,185,185,0.36)}.module-signin-btn.module-signin-disable{cursor:not-allowed;opacity:0.6}